.navbar__ul p{
       animation: animate 5s linear infinite;
   }

   .navbar__ul p:nth-child(even){
       animation-delay: 0.4s;
   }
   @keyframes animate {
       0%,18%, 20%, 50.1%, 60%, 65.1%, 80%, 90.1%, 92%
       {
           color: #113641;
           text-shadow: none;
       }    
       18.1%,20.1%,30%, 50%, 60.1%, 65%, 80.1%, 90%, 92.1%,100%
       {
           color:#fff;
           text-shadow: 0 0 10px #00ff00,
           0 0 20px #00ff00,
           0 0 40px #00ff00,
           0 0 80px #00ff00,
           0 0 160px #00ff00,
           0 0 400px #00ff00;
        }
       
       }
