.cardsp{
    background-color: #222;
}

.cards{
    width: 80%;
    padding-top: 30px;
    height: max-content;
    margin: auto;
    border: 2px solid green;
    text-align: center;
    border-radius: 10px;
    color: white;
  }
  link{
    text-decoration: none;
  }
  .cardsp h3{
    padding: 10px;
    font-size: 30px;
  }
  .cardsp p{
    padding: 5px;
    font-size: 20px;
    color: #00ff00;
  }
  button{
    width: 100px;
    background-color: black;
    color: #00ff00;
    border: 1px solid #00ff00;
    border-radius: 5px;
    height: 40px;
    margin-top:20px;
    font-size: 18px;
    font-weight: 600;
  }
  .cards img {
    width: 100%;
    height: 300px;
  }
  .row{
    width: 90% !important;
    display: flex;
  }