.cardsp{
    background-color: #222;
}
.container{
    width: 100%;
    height: 100vh;
    background-size: 100% 100%;
}
video{
    width: 100% !important;
    height: 100vh;
    position: absolute;
    z-index: -12312;
    padding-top:100px ;
}
.cards{
    width: 100%;
    padding-top: 30px;
    height: max-content;
    margin: auto;
    border: 2px solid green;
    text-align: center;
    border-radius: 10px;
    background-color: #111111;
    color: white;
  }
  .cardss{
    display: grid;
    grid-template-columns: repeat(4,1fr);
  }
  .cardsp h3{
    padding: 10px;
    font-size: 30px;
  }
  .cardsp p{
    padding: 5px;
    font-size: 20px;
    color: #00ff00;
  }
  button{
    width: 100px;
    background-color: black;
    color: #00ff00;
    border: 1px solid #00ff00;
    border-radius: 5px;
    height: 40px;
    margin-top:20px;
    margin-bottom: 20px;
    font-size: 18px;
    font-weight: 600;
  }

  .cards img {
    width: 100%;
    height: 300px;
  }
 